//
// Badge
// --------------------------------------------------

.badge {
  --si-badge-padding-x: 0.7em;
  --si-badge-padding-y: 0.4em;
}

// Fix secondary and light badges colors

.badge.bg-secondary,
.badge.bg-light {
  --#{$prefix}badge-color: #{$gray-800};
}


// Badge link

a.badge {
  text-decoration: none;
}


// Fix badge alignment inside button

.btn .badge {
  top: 1px;
}
