html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html *, h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: als-sirius, sans-serif;
  letter-spacing: -0.01rem;
  --si-font-sans-serif: als-sirius, sans-serif;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.page-wrapper {
  flex: 1 0 auto;
}

.footer {
  flex-shrink: 0;
}

:root {
  --si-user-selection-color: rgba(var(--si-primary-rgb), 0.22);
  --si-heading-link-color: #33354d;
  --si-heading-link-hover-color: #0271ff;
}

a {
  transition: color 0.2s ease-in-out;
}
a:focus {
  outline: none;
}

a[class^=bg-]:not(.btn),
a[class*=bg-]:not(.btn) {
  transition: background-color 0.25s ease;
}

img,
figure {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

svg {
  max-width: 100%;
}

.dark-mode-img {
  display: none;
}

iframe {
  width: 100%;
}

::selection {
  background: var(--si-user-selection-color);
}

::-moz-selection {
  background: var(--si-user-selection-color);
}

b,
strong {
  font-weight: 700;
}

small, .small {
  font-weight: inherit;
}

button:focus {
  outline: none;
}

@media (min-width: 992px) {
  .docs-container {
    padding-left: 21rem;
  }
}
@media (min-width: 1400px) {
  .docs-container {
    padding-right: 20rem;
  }
}

.lg-icon {
  font-family: "Font Awesome 6 Pro" !important;
}
.lg-icon.lg-fullscreen:after {
  content: "\f065";
}
.lg-fullscreen-on .lg-icon.lg-fullscreen:after {
  content: "\f066";
}
.lg-icon.lg-close:after {
  content: "\f00d";
}
.lg-icon.lg-zoom-in:after {
  content: "\f00e";
}
.lg-icon.lg-actual-size:after {
  content: "\f002";
}
.lg-icon.lg-zoom-out:after {
  content: "\f010";
}
.lg-icon.lg-next:before {
  content: "\f061";
}
.lg-icon.lg-prev:after {
  content: "\f060";
}

h4, .h4, .fs-4,
h5, .h5, .fs-5,
h6, .h6, .fs-6 {
  line-height: 1.4;
